body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
  Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

nav {
  display: flex;
  padding: 2rem 0;
}

nav section {
  width: 100%;
}

nav a {
  font-weight: 700;
  padding: 1rem 1.5rem;
  border-radius: 4px;
}

nav a:first-of-type {
  margin-left: -1.5rem;
}

/*nav a:hover {
  background: rgba(82, 150, 206, 0.959);
}*/

a {
  text-decoration: none;
}


div.noticia {
  width: auto;
  margin: auto;
  padding: 15px;
}

div.noticia img.izquierda {
  float: left;
  margin-right: 15px;
}

section {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1.5rem;
}

section h1 {
  font-size: 3rem;
}

input, select, textarea {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

textarea {
  width: 100%;
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  resize: none;
}

.question h2 {
  font-size: 2.5rem;
}

.question-excerpt {
  padding: 1.5rem 0;
  border-bottom: 1px solid #eee;
}

.question-excerpt:first-of-type {
  padding-top: 0;
}

.question-excerpt h2 {
  margin-top: 0;
  font-size: 1.5rem;
}

.answer {
  background: #f0f0f062;
  border-radius: 4px;
  padding: 1rem 1.5rem;
  margin: 1rem 0;
}


.button {
  display: inline-block;
  background: #ea3546;
  color: white;
  border-radius: 4px;
  font-weight: 700;
  padding: 0.75rem 1.5rem;
}

.right {
  float: right;
}

.login {
  min-height: 82vh;
  margin-top: auto;
  margin-bottom: auto;
}

.bg-image {
  background-image: url('https://sites.google.com/site/eportafolioluisangel/_/rsrc/1485745186209/indirect-questions/4080354b8aa40b5c97e249598b4604f9_question-mark-icon-png-clipart-question-clipart-png_1500-1500.png?height=300px&width=100%25');
  background-size: cover;
  background-position: center;
}


.btn-login {
  font-size: 0.9rem;
  letter-spacing: 0.05rem;
  padding: 0.75rem 1rem;
}

.linkRegister {
  color: #ea3546;
}

.card{
  background: #2c0c66;
  max-width: 60rem;
}

.card-answer{
  background: #5d35a8;
  max-width: 60rem;
}

.container-md {
  background-color: #2e1857;
}

.container-profile {
  background-color: #422e6b;
}

.title-profile {
  font-size: 40px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.img-profile {
  border-radius: 50%;
}

.navbar-bg {  
  background-color: #422e6b;
}

.footer-style {
  background-color: #211735;
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 900;
  width: 100%;
}

.a-footer {
  font-size: 16px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  margin: 0%;
}

.p-footer {
  font-size: 15px;
  font-family: 'Courier New', Courier, monospace;
  margin: 0%;
}

.logo {
  width: 180px;
  margin-left: 40px;
}

.container-question-title {
  align-items: center;
  justify-content: space-between;
}

.container-favorites {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 5px;
  border: 1px solid #777474;
  min-width: 70px;
  border-radius: 4px;
}

.container-favorites-added {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 5px;
  border: 1px solid #2c2a2a;
  background: #424041;
  color: white;
  min-width: 70px;
  border-radius: 4px;
}

.container-favorites:hover {
  background: #424041;
  color: white;
  transition: all 0.3s;
  border: 1px solid black;
}

.text-favorite {
  margin: 0;
}

.container-fav{
  margin-top: 3%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #2e1857;
  font-weight: 800;
  font-size: 20px;
  padding: 1.5rem 1rem;
  border-bottom: 1px solid rgb(97, 193, 223);
}

.button-rm-fav{
  height: 35px;
  background: #e02e2e;
  color: white;
  font-weight: 700;
  border: 1px solid rgb(0, 0, 0);
  width: 100px;
  cursor: pointer;
}

.answer-fav {
  font-size: 25px;
}

.keyboard{
  cursor: pointer;
  height: 35px !important;
  width: 40px !important;
  color:rgb(44, 40, 40);
}

.keyboard:hover{
  background-color: rgb(211, 206, 206);
  border-radius: 50%;
  transition: background .5s;
}
